body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
		overflow-y: hidden;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.rdw-editor-toolbar {
	border: none !important;
	border-bottom: 1px solid #ccc !important;
	border-radius: 0 !important;
	margin-bottom: 0 !important;
	display: grid !important;
	grid-template-columns: auto auto auto auto auto auto auto;
	overflow-x: auto;
}

.rdw-dropdown-selectedtext {
	font-size: 12px;
}

.rdw-editor-wrapper {
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height: 100%;
	border: none;
	border-radius: 2px;
}

.rdw-editor-main {
  overflow: unset !important;
  /* height: calc(100% - 60px) !important; */
}

.DraftEditor-editorContainer {
	height: calc(100% - 65px) !important;
	overflow-y: auto;
	padding: 0 20px 0 20px;
}

.rdw-link-wrapper {
	display: grid !important;
	grid-template-columns: auto auto;
}

.rdw-list-wrapper {
	display: grid !important;
	grid-template-columns: auto auto;
}

.rdw-text-align-wrapper,
.rdw-inline-wrapper {
	display: grid !important;
	grid-template-columns: auto auto auto auto;
}

.rdw-dropdown-optionwrapper {
	position: absolute !important;
	width: inherit !important;
}

.rdw-colorpicker-wrapper,
.rdw-link-wrapper {
	position: inherit !important;
}

.rdw-colorpicker-modal {
	top: 43px !important;
	left: 70px !important;
	border: 1px solid #00000029 !important;
}

.rdw-link-modal {
	top: 43px !important;
	left: 45px !important;
  height: 145px !important;
  border: 1px solid #ccc !important;
}

.rdw-link-modal-target-option {
  display: none;
}

.rdw-link-modal-label {
  font-size: 12px;
  color: #333333;
  margin-bottom: 0;
  line-height: 1.1;
  font-family: 'Roboto', sans-serif;
}

.rdw-option-active,
.rdw-option-wrapper:hover {
  box-shadow: 35px 1px 0px #ccc inset !important;
}

.rdw-link-modal-input,
.rdw-colorpicker-modal,
.rdw-dropdown-wrapper,
.rdw-dropdown-optionwrapper {
  border: 1px solid #ccc !important;
}

.draftjs-editor-container .rdw-left-aligned-block > div,
.draftjs-editor-container .rdw-center-aligned-block > div,
.draftjs-editor-container .rdw-right-aligned-block > div,
.draftjs-editor-container .rdw-justify-aligned-block > div {
  display: contents !important;
}

.draftjs-editor-container .rdw-left-aligned-block,
.draftjs-editor-container .rdw-center-aligned-block,
.draftjs-editor-container .rdw-right-aligned-block,
.draftjs-editor-container .rdw-justify-aligned-block {
  margin-bottom: 16px !important;
}

.draftjs-editor-container .rdw-colorpicker-modal-style-label {
  overflow-wrap: normal;
}

.draftjs-editor-container .rdw-option-active,
.draftjs-editor-container .rdw-option-wrapper:hover {
  box-shadow: none;
  background: #E2E2E2;
  border: none;
}

.draftjs-editor-container .rdw-option-wrapper {
  border: none;
}

.draftjs-editor-container .rdw-inline-wrapper .rdw-option-wrapper 
.draftjs-editor-container .rdw-link-wrapper .rdw-option-wrapper 
.draftjs-editor-container .rdw-list-wrapper .rdw-option-wrapper  {  
  padding: 10px 5PX;
}

.draftjs-editor-container .rdw-link-modal-label {
  font-size: 12px;
  color: #333333;
  margin-bottom: 0;
  line-height: 1.1;
  font-family: 'Roboto', sans-serif;
}

.draftjs-editor-container .rdw-link-modal-input {
  font-size: 12px;
  height: 23px;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
  color: #333333;
  outline: none;
  padding-left: 3px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.draftjs-editor-container .rdw-link-modal-buttonsection {
  margin: 0;
  text-align: right;
}

.draftjs-editor-container .rdw-link-modal-buttonsection .rdw-link-modal-btn {  
  border-radius: 3px;
  outline: none;
  padding: 0 9px;
  height: 24px;
  font-size: 12px;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  width: auto;
  box-shadow: none;
}

.draftjs-editor-container .rdw-link-modal-buttonsection .rdw-link-modal-btn:first-child {  
  background: #003D71;
  border: 1px solid #003D71;
  color: #ffffff;
}

.draftjs-editor-container .rdw-link-modal-buttonsection .rdw-link-modal-btn:first-child:disabled  {  
  cursor: not-allowed;
  color: #99CCFF;
  background: #6699CC;
  border: 1px solid #6699CC;
}

.draftjs-editor-container .rdw-link-modal-buttonsection .rdw-link-modal-btn:last-child {  
  background: #ffffff;
  color: #666666;
  border: 1px solid #cccccc;
}

.draftjs-editor-container .rdw-dropdown-wrapper:active, .draftjs-editor-container .rdw-dropdown-wrapper:hover {
  box-shadow: none;
}

.draftjs-editor-container .rdw-dropdownoption-default {
  font-size: 12px;
  font-family: 'Roboto', sans-serif;
}

.draftjs-editor-container .rdw-dropdownoption-highlighted {
  background: #cccccc;
}

.draftjs-editor-container .rdw-colorpicker-modal-options {
  overflow-x: unset;
} 

.draftjs-editor-container .rdw-dropdown-wrapper {
  height: 23px;
}

.draftjs-editor-container .rdw-dropdown-carettoopen, .draftjs-editor-container .rdw-dropdown-carettoclose {
  right: 5px;
}

.rdw-suggestion-option-active {
  background: #e2e2e2;
}

.draft-mention-avatar {
	background: #E2E2E2;
    color: #999999;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
}

.display-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.mention-name {
    width: 100%;
    padding: 0 2px 2px 7px;
    white-space: nowrap;
    width: 170px;
    overflow: hidden;
    text-overflow: ellipsis;
}
